body {
  background-color:#A9CCE3 ;
}
.app {
  text-align:center;
}
.container {
  background-color: rgb(7,2,71);
  justify-content: center;
  width:50%;
  margin-left:26%;
  margin-top:5%;
  text-align: center;
  padding:20px;
}
input[type=text], select {
  width:80%;
  padding:12px 20px;
  margin:30px 0;
  display:inline-block;
  border:1px solid #ccc;
  border-radius:4px;
  box-sizing:border-box;
}
button {
  width:15%;
  background-color: #4CAF50;
  color:#000;
  padding:14px 20px;
  margin:8px 0;
  border:none;
  border-radius:4px;
  cursor: pointer;
  font-weight:bold;
}
button:hover { 
  background-color: #45a049;
}
.header {
  width:100%;
  padding: 12px 20px;
  color:#fff;
  font-size: 50px;
}
.List-heading {
  color:#000;
  font-size: 50px;
  text-align: left;
  width: 60%;
  background-color: cadetblue;
}
.list-data {
  color:#000;
  font-size: 20px;
  background-color:rgb(164,203,237);
  text-align: left;
  padding-left:30px;
  width:53%;
  padding-bottom:2%;
  padding-top:2%;
  border-radius:10px;
}
.btn-position {
  position:relative;
  left:11%;
  top:-54%;
}







